@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=block);
:root {
  --white: #f9f9f9;
  --black: #141414;
  --purple: #e301be;
  --dark-blue: #360c86;
  --light-blue: #6b00ff;
}

body {
  margin: 0;
  font-family: Roboto;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body,
#root {
  width: 100%;
  height: 100%;
  background-color: #141414;
}


.App__rotate-button {
  position: absolute;
  background-color: #6b00ff;
  background-color: var(--light-blue);
  padding: 10px;
  color: #f9f9f9;
  color: var(--white);
  border: 0;
  left: 50%;
  top: 20px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  font-size: 20px;
  font-weight: 400;
  cursor: pointer;
  border-radius: 5px;
}

.App__rotate-square {
  width: 100px;
  height: 100px;
  background: #e301be;
  background: var(--purple);
}

.App__rotate-square-inner {
  width: 50px;
  height: 50px;
  background: #360c86;
  background: var(--dark-blue);
}

.App__stats-container {
  text-align: left;
  font-size: 13px;
  background-color: #360c86;
  background-color: var(--dark-blue);
  color: #f9f9f9;
  color: var(--white);
  padding: 20px;
}

.App__stats-value {
  position: relative;
  display: inline-block;
  padding: 3px;
  background-color: #6b00ff;
  background-color: var(--light-blue);
  color: #f9f9f9;
  color: var(--white);
}

