@import '../../styles/shared.scss';
.Button.open {
  background: #E800B2;
}

.Button {
  width: 130px;
  height: 50px;
  background: #2F0C77;
  border: 2px solid #FFFFFF;
  box-sizing: border-box;
  border-radius: 11px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;
  color: #FFFFFF;
  margin: 8px 10px;
}
